"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/contexts/auth-context";
import { useToast } from "@/contexts/toast-context";
import { Eye, EyeOff, Mail, Lock, User, Shield, ArrowRight } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

export default function LoginPage() {
    const router = useRouter();
    const { user, signInWithPassword, signUpWithPassword, operationLoading } = useAuth();
    const { addToast } = useToast();

    const [isLogin, setIsLogin] = useState(true);
    const [email, setEmail] = useState("");
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [showPassword, setShowPassword] = useState(false);
    const [focusedInput, setFocusedInput] = useState<string | null>(null);

    useEffect(() => {
        if (user) {
            router.replace("/chat");
        }
    }, [user, router]);

    const handleAuth = async (e: React.FormEvent) => {
        e.preventDefault();

        if (isLogin) {
            if (!email || !password) {
                addToast("Please fill in all fields", "error");
                return;
            }
            const { error } = await signInWithPassword(email, password);
            if (error) addToast(error, "error");
        } else {
            if (!email || !password || !username) {
                addToast("Please fill in all fields", "error");
                return;
            }
            if (password !== confirmPassword) {
                addToast("Passwords do not match", "error");
                return;
            }
            const { error, needsEmailConfirmation } = await signUpWithPassword(email, password, {
                username,
                full_name: username,
            });
            if (error) {
                addToast(error, "error");
            } else if (needsEmailConfirmation) {
                addToast("Check your email to confirm your account", "success");
            }
        }
    };

    return (
        <div className="relative min-h-screen bg-black text-white flex flex-col items-center justify-center p-6 overflow-hidden">
            {/* Background Blobs */}
            <div className="absolute top-[-10%] right-[-10%] w-[500px] h-[500px] bg-primary/20 rounded-full blur-[120px] animate-pulse" />
            <div className="absolute bottom-[10%] left-[-10%] w-[400px] h-[400px] bg-secondary/10 rounded-full blur-[100px] animate-pulse" />

            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="w-full max-w-md z-10"
            >
                {/* Header */}
                <div className="mb-8 space-y-2">
                    <h1 className="text-4xl font-black tracking-tight">
                        {isLogin ? "Welcome Back" : "Create Account"}
                    </h1>
                    <p className="text-text-secondary font-medium">
                        {isLogin ? "Sign in to continue chatting" : "Join the conversation today"}
                    </p>
                </div>

                {/* Form Card */}
                <div className="glass-card border border-white/10 rounded-[32px] p-8 backdrop-blur-2xl bg-white/5 shadow-2xl">
                    {/* Toggle */}
                    <div className="flex bg-white/5 p-1.5 rounded-2xl mb-8">
                        <button
                            onClick={() => setIsLogin(true)}
                            className={`flex-1 py-3 px-4 rounded-xl text-sm font-bold transition-all duration-300 ${isLogin ? "bg-white text-black shadow-lg" : "text-white hover:bg-white/5"
                                }`}
                        >
                            Login
                        </button>
                        <button
                            onClick={() => setIsLogin(false)}
                            className={`flex-1 py-3 px-4 rounded-xl text-sm font-bold transition-all duration-300 ${!isLogin ? "bg-white text-black shadow-lg" : "text-white hover:bg-white/5"
                                }`}
                        >
                            Signup
                        </button>
                    </div>

                    <form onSubmit={handleAuth} className="space-y-4">
                        <div className="space-y-4">
                            {/* Email Input */}
                            <div className={`relative group transition-all duration-300 rounded-2xl border ${focusedInput === 'email' ? 'border-primary bg-primary/5' : 'border-white/10 bg-white/5'
                                }`}>
                                <div className="absolute left-4 top-1/2 -translate-y-1/2">
                                    <Mail size={20} className={focusedInput === 'email' ? 'text-primary' : 'text-text-secondary'} />
                                </div>
                                <input
                                    type="email"
                                    placeholder="Email Address"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    onFocus={() => setFocusedInput('email')}
                                    onBlur={() => setFocusedInput(null)}
                                    className="w-full h-14 bg-transparent pl-12 pr-4 outline-none text-white font-medium placeholder:text-white/30"
                                />
                            </div>

                            {/* Username Input (Signup only) */}
                            <AnimatePresence>
                                {!isLogin && (
                                    <motion.div
                                        initial={{ opacity: 0, height: 0 }}
                                        animate={{ opacity: 1, height: "auto" }}
                                        exit={{ opacity: 0, height: 0 }}
                                        className="relative group transition-all duration-300 rounded-2xl border border-white/10 bg-white/5"
                                    >
                                        <div className="absolute left-4 top-1/2 -translate-y-1/2">
                                            <User size={20} className="text-text-secondary" />
                                        </div>
                                        <input
                                            type="text"
                                            placeholder="Username"
                                            value={username}
                                            onChange={(e) => setUsername(e.target.value)}
                                            className="w-full h-14 bg-transparent pl-12 pr-4 outline-none text-white font-medium placeholder:text-white/30"
                                        />
                                    </motion.div>
                                )}
                            </AnimatePresence>

                            {/* Password Input */}
                            <div className={`relative group transition-all duration-300 rounded-2xl border ${focusedInput === 'pass' ? 'border-primary bg-primary/5' : 'border-white/10 bg-white/5'
                                }`}>
                                <div className="absolute left-4 top-1/2 -translate-y-1/2">
                                    <Lock size={20} className={focusedInput === 'pass' ? 'text-primary' : 'text-text-secondary'} />
                                </div>
                                <input
                                    type={showPassword ? "text" : "password"}
                                    placeholder="Password"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                    onFocus={() => setFocusedInput('pass')}
                                    onBlur={() => setFocusedInput(null)}
                                    className="w-full h-14 bg-transparent pl-12 pr-12 outline-none text-white font-medium placeholder:text-white/30"
                                />
                                <button
                                    type="button"
                                    onClick={() => setShowPassword(!showPassword)}
                                    className="absolute right-4 top-1/2 -translate-y-1/2 text-text-secondary hover:text-white transition-colors"
                                >
                                    {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                                </button>
                            </div>

                            {/* Confirm Password (Signup only) */}
                            {!isLogin && (
                                <div className="relative group transition-all duration-300 rounded-2xl border border-white/10 bg-white/5">
                                    <div className="absolute left-4 top-1/2 -translate-y-1/2">
                                        <Shield size={20} className="text-text-secondary" />
                                    </div>
                                    <input
                                        type={showPassword ? "text" : "password"}
                                        placeholder="Confirm Password"
                                        value={confirmPassword}
                                        onChange={(e) => setConfirmPassword(e.target.value)}
                                        className="w-full h-14 bg-transparent pl-12 pr-4 outline-none text-white font-medium placeholder:text-white/30"
                                    />
                                </div>
                            )}
                        </div>

                        {/* Submit Button */}
                        <button
                            type="submit"
                            disabled={operationLoading}
                            className="w-full mt-6 h-14 rounded-2xl font-bold text-lg relative overflow-hidden group disabled:opacity-50"
                        >
                            <div className="absolute inset-0 bg-gradient-to-r from-primary to-blue-600 transition-transform duration-300 group-hover:scale-105" />
                            <span className="relative flex items-center justify-center gap-2">
                                {operationLoading ? (
                                    <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                ) : (
                                    <>
                                        {isLogin ? "Sign In" : "Create Account"}
                                        <ArrowRight size={20} />
                                    </>
                                )}
                            </span>
                        </button>
                    </form>

                    {isLogin && (
                        <button className="w-full mt-6 text-primary text-sm font-bold hover:underline transition-all">
                            Forgot Password?
                        </button>
                    )}
                </div>

                {/* Footer Toggle */}
                <p className="mt-8 text-center text-text-tertiary">
                    {isLogin ? "Don't have an account? " : "Already have an account? "}
                    <button
                        onClick={() => setIsLogin(!isLogin)}
                        className="text-primary font-black hover:underline ml-1"
                    >
                        {isLogin ? "Sign Up" : "Log In"}
                    </button>
                </p>
            </motion.div>
        </div>
    );
}
